.total-fiat {
  text-align: center;
}

.title {
  text-align: center;
}

#bitcoin {
  grid-area: bitcoin;
}
#monero {
  grid-area: monero;
}
#ethereum {
  grid-area: ethereum;
}

.price-container .title {
  grid-area: title;
}


.crypto-container {
  display: grid;
  justify-items: center;
  grid-template-areas: 
  "title"
  "bitcoin"
  "monero"
  "ethereum";
}
