.price-container {
  border: 1px solid white;
  margin-bottom: 2ch;
  text-align: center;
  background: #4a4a4a;
  width: 70%;
  grid-template-areas:
      "a b c d";
  display: grid;
  align-items: center;
}

@media (max-width: 799px) {
  .price-container {
    width: 90%;
    grid-template-areas:
    "a b"
    "c d";
    padding-bottom: 1ch;
  }

  h2 {
      font-size: 1rem;
  }
  .price-container div, .price-container span {
      font-size: 0.8rem;
  }
}