.input-container {
  text-align: center;
  display: grid;
  grid-template-areas:
  "a b";
  grid-template-columns: 25% 10%;
  grid-template-rows: 3ch;
  justify-content: center;
}

.result-value {
  text-align: center;
}

@media (max-width: 799px) {
  .input-container {
    grid-template-columns: 60% 10%;
    grid-template-rows: 4ch;
  }
}

@media (max-width: 425px) {
  .input-container {
    grid-template-columns: 50% 25%;
  }
}